<template>
    <div class="dp_button">
        <button :class="{small:size==='small',medium:size==='medium',large:size==='large'}"
                :style="{backgroundColor: backgroundColor ? backgroundColor : buttonType.backgroundColor,color:buttonType.color}"
                class="btn">
            <slot></slot>
        </button>

    </div>
</template>

<script>
    const allTypes = {
        primary: {
            backgroundColor: ' #fd601a',
            color: 'white',
        },
        success: {
            backgroundColor: 'rgb(103, 194, 58)',
            color: 'white',
        },
        danger: {
            backgroundColor: 'rgb(245, 108, 108)',
            color: 'white',
        }
    };
    export default {
        name: "index",
        data() {
            return {
                buttonType: allTypes[this.type],
            }
        },
        props: {
            backgroundColor: {
                type: String,
            },
            type: {
                type: String,
                default: 'primary',
            },
            size: {
                default: "large",
                type: String,
            },
        }
    }
</script>

<style scoped lang="scss">
    .dp_button {
        display: inline-block;

        .btn {
            min-width: 300px;
            /*height: 150px;*/
            padding: 30px 60px;
            background-color: #fd601a;
            border-radius: 75px;
            text-align: center;
            font-size: 50px;
            border: none;
            color: white;
        }

        .small {
            border-radius: 30px;
            min-width: 50px;
            font-size: 40px;
            padding: 20px 100px;
            box-sizing: border-box;
        }
        .medium{
            min-width: 500px;
            padding: 40px 60px;

        }

        .large{
            width: 570px;
            padding: 50px 60px;
        }

        .mini {

        }

    }

</style>
