<template>
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight}">
        <DP_Register v-if="showRegister"></DP_Register>
    </div>
</template>

<script>

    import {loginbyWechatCode} from "@/api/login";
    // eslint-disable-next-line no-unused-vars
    import {getQueryString, getLocalData, setLocalData, wechat_login} from "@/utils/function";
    import {mapGetters} from "vuex";
    import DP_Register from "@/components/DP_Register/index";
    import pageMixin from "@/mixin/pageMixin";
    import {sernsorFn} from '@/utils/sensors';

    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title, // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "wechat",
        components: {
            DP_Register,
        },
        mixins: [pageMixin],
        props: {},
        data() {
            return {}
        },
        watch: {},
        computed: {
            ...mapGetters({
                showRegister: "getShowRegister",
            }),
        },
        methods: {

            getUserInfo(wechat_code) {
                loginbyWechatCode(wechat_code).then((res) => {
                    // eslint-disable-next-line no-unused-vars
                    const {headers, data: {code, data}} = res;
                    const auth = headers['Authorization'] || headers['authorization'];

                    if (code === 0) {
                        this.$store.commit('setUserInfo', data);
                        if (auth) {
                            setLocalData('referral-token', JSON.stringify(auth), 'cookies');
                            this.$store.commit('setToken', auth);
                            sernsorFn('login');
                            this.jump();
                        } else {
                            this.$store.commit('setshowRegister', true);
                        }

                    }

                })


            },
            jump() {
                const redirect = getLocalData('dp-redirect', 'session');
                if (redirect) {
                    console.log('即将重定向到', redirect);
                    this.$router.push(redirect);
                } else {
                    //未获取到重定向路径 就跳到个人中心
                    this.dp_go('account-center');
                }

            }
        },
        created() {
            console.log("进入微信授权页面")
            const wechat_code = getQueryString("code");
            const inWechat = this.$store.getters.getBrowser.device.isWechat;//为了预渲染 需判断一下
            if (inWechat) {
                if (wechat_code) {
                    this.getUserInfo(wechat_code);
                } else {
                    console.log("跳转微信")
                    wechat_login();
                }
            } else {
                console.log('请在微信中打开此页面');
            }
        },
        mounted() {

        },
    }
</script>

<style lang='scss' scoped>
    .view-wrapper {
        /*background-image: url("../../assets/global/material/login_bg.jpg");*/
        /*background-size: 100% 100%;*/
        /*background-size: cover;*/
        /*background-position: center center;*/

    }
</style>
