<template>
    <div class="DP_Register">
        <DP_Popup :display="true" :show-close="false" title="">
            <div class="slogan">
                绑定手机号 <br>即可参与推荐活动
            </div>
            <div class="panda"></div>
            <div class="login">
                <input type="text" v-model="phone" placeholder="请输入手机号码">
                <input type="text" v-model="code" placeholder="验证码">
                <div class="time" @click="countdown">{{seconds!==60 ? seconds+'s' :'获取验证码'}}</div>
            </div>
            <template slot="button">
                <DP_Button class="login_btn" @click.native="login">绑定</DP_Button>
                <div class="login_tips">绑定手机号将为您自动创建熊猫博士账户</div>
            </template>

        </DP_Popup>
    </div>
</template>

<script>
    import DP_Popup from '@/components/DP_Popup'
    import DP_Button from '@/components/DP_Button'
    import {bindPhone, getMobileCode} from "@/api/login";
    import {checkPhone, getLocalData, setLocalData} from "@/utils/function";
    import {sernsorFn} from "@/utils/sensors";
    import errorCode from "@/utils/errorCode";

    export default {
        name: "index",
        components: {
            DP_Popup,
            DP_Button,
        },
        props: {},
        data() {
            return {
                seconds: 60,
                secondsLabel: "获取验证码",
                sendCodeIsOk: false,//
                code: '',
                phone: '',
                phoneIsOk: false,
            }
        },
        watch: {},
        computed: {},
        methods: {
            sendMessage() {
                if (!checkPhone(this.phone)) {
                    return this.$toast("手机号码格式不正确");
                }
                getMobileCode(this.phone).then(res => {
                    const {data: {code}} = res;
                    this.sendCodeIsOk = true;
                    if (code === 0) this.$toast('验证码已发送');
                })
            },
            login() {
                if (!checkPhone(this.phone)) {
                    return this.$toast("手机号码格式不正确");
                }
                if (!this.code) {
                    return this.$toast("验证码未填写");
                }

                if (!this.sendCodeIsOk) {
                    return this.$toast("验证码校验错误");
                }

                const user = this.$store.getters.getUserInfo;
                if (user.unionId) {
                    bindPhone(this.phone, this.code, user.avatar, user.name, user.unionId)
                        .then(res => {
                            const {headers, data: {code, message}} = res;
                            const auth = headers['Authorization'] || headers['authorization'];

                            console.log(message);
                            if (code === 0) {
                                sernsorFn('signUp');
                                this.$store.commit('setshowRegister', false);
                                // this.$toast.success('绑定成功');
                                setLocalData('referral-token', JSON.stringify(auth), 'cookies');
                                this.$store.commit('setToken', auth);
                                this.$toast('绑定成功');
                                const redirect = getLocalData('dp-redirect', 'session');
                                if (redirect) {
                                    console.log('即将重定向到', redirect);
                                    this.$router.push(redirect);
                                } else {
                                    console.log('未获取到重定向信息 重定向到个人中心')
                                    this.dp_go('account-center');
                                }
                            } else {
                                this.$store.commit('setGlobalTips', {show: false})//由于这里使用$toast弹出，不显示全局提示 以 避免与全局处理的错误提示冲突
                                const msg = errorCode[code] || '请重试';
                                this.$toast(msg);
                            }
                        });

                } else {
                    this.$toast('本地信息获取失败');
                }
            },
            countdown() {
                if (this.seconds === 60 && (this.phoneIsOk = checkPhone(this.phone))) {
                    this.sendMessage();
                    const _this = this;

                    // eslint-disable-next-line no-inner-declarations
                    function timer() {  //每隔1秒让++i
                        if (_this.seconds - 1 <= 0) {
                            _this.seconds = 60;
                            return;
                        }
                        _this.seconds--;
                        setTimeout(timer, 1000);
                    }

                    timer();

                    //
                    // const timer = setTimeout(() => {
                    //     if (this.seconds - 1 <= 0) {
                    //         this.seconds = 60;
                    //         clearTimeout(timer);
                    //     } else {
                    //         this.seconds--;
                    //         timer
                    //     }
                    //
                    // }, 1000)
                } else if (!checkPhone(this.phone)) {
                    this.$toast("手机号码格式不正确");
                } else if (this.seconds !== 60) {
                    this.$toast("操作频繁 " + this.seconds + "秒后再试");
                }
            }
        },
        created() {
        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
    .DP_Register {
        position: relative;

        .panda {
            background-image: url("../../assets/global/material/panda.png");
            background-size: 100% 100%;
            background-size: cover;
            width: 300px;
            height: 385px;
            position: absolute;
            top: -180px;
            right: 0;
        }

        .slogan {
            width: 420px;
            height: 135px;
            color: #fc601a;
            font-size: 50px;
            font-weight: bold;
            line-height: 68px;
            text-align: center;
        }

        .login {
            position: relative;

            input {
                font-size: 39px;
                box-sizing: border-box;
                margin-top: 30px;
                height: 138px;
                /*line-height: 138px;*/
                width: 100%;
                background-color: #f8f8f8;
                border-radius: 69px;
                padding: 0px 60px;
                vertical-align: middle;
            }

            .time {
                width: 240px;
                height: 90px;
                line-height: 90px;
                background-color: #70b9fd;
                border-radius: 45px;
                color: white;
                font-size: 36px;
                position: absolute;
                right: 10px;
                bottom: 20px;

            }

        }

        .login_tips {
            font-size: 33px;
            color: #6e6e6e;
            margin-top: 30px;
        }

    }
</style>
